<template>
	<view class="body">
		<!-- 搜索 -->
		<view class="search">
			<img src="../../static/volunteer/seacher_icon.png" alt="搜索">
			<input type="text" placeholder-class="defaultInput" placeholder="请输入关键词搜索" v-model="searchText"
				@confirm="search">
		</view>
		<view class="info">
			<view>{{city}}</view>
			<view v-show="city!='请选择'">{{school}}</view>
		</view>
		<view class="tip">
			<view class="city">请选择城市</view>
			<view class="school">请选择学校</view>
		</view>
		<view class="list">
			<view class="listLeft">
				<view :class="{cell:true,checked:currentCity===index}" v-for="(item,index) in areaList" :key="index" @click="checkCity(item,index)">{{item.name}}</view>
			</view>
			<view class="listRight">
				<view :class="{cell:true,checked:currentSchool===index}" v-for="(item,index) in schoolList" :key="index" @click="checkSchool(item,index)">{{item.name}}</view>
			</view>
		</view>
		<view class="sure" @click="backComplete">确定</view>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { area, school, searchSchool} from '@/common/http.api.js';
	export default {
		data() {
			return {
				searchText:'', //搜索关键词
				city:'请选择', //城市
				school:'请选择', //院校
				currentCity:'', 
				currentSchool:'', 
				area_id:'',
				school_id:'',
				areaList:[],
				schoolList:[],
			}
		},
		onLoad() {
			area().then(res=>{
				console.log(res)
				this.areaList = res.data.data
				school({area:res.data.data[0].name}).then(res=>{
					console.log(res)
					this.schoolList = res.data.data
				}).catch(err=>{
					console.log(err)
				})
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			//选择城市
			checkCity(item,index) {
				this.city = item.name
				this.currentCity = index
				this.area_id = item.id
				school({area:item.name}).then(res=>{
					console.log(res)
					this.schoolList = res.data.data
				}).catch(err=>{
					console.log(err)
				})
				this.school = '请选择'
			},
			//选择院校
			checkSchool(item,index) {
				if(this.city=='请选择'){
					this.$u.toast('请先选择上一级')
				}else{
					this.currentSchool = index
					this.school = item.name
					this.school_id = item.id
				}
			},
			//搜索
			search() {
				this.currentCity = ''
				this.city = ''
				this.currentSchool = ''
				this.school = ''
				searchSchool({school:this.searchText}).then(res=>{
					console.log(res)
					this.schoolList = res.data.data
				}).catch(err=>{
					console.log(err)
				})
			},
			// 回到填写页面
			backComplete() {
				uni.navigateBack({
					delta:1
				})
				uni.setStorage({
					key: 'area_id',
					data: this.area_id,
					success: function () {
						console.log('success');
					}
				});
				uni.setStorage({
					key: 'school_id',
					data: this.school_id,
					success: function () {
						console.log('success');
					}
				});
				uni.setStorage({
					key: 'school',
					data: this.school,
					success: function () {
						console.log('success');
					}
				});
				/* uni.redirectTo({
					url:'/pages/volunteer/complete?area_id='+this.area_id+'&school_id='+this.school_id+'&school='+this.school
				}) */
			}
		}
	}
</script>

<style lang="scss">
	.body{
		.search {
			width: 90%;
			margin: 30rpx 5%;
			height: 78rpx;
			border: 1px solid #c7c7c7;
			border-radius: 100rpx;
			display: flex;
			align-items: center;
			background-color: #fff;
		
			img {
				margin-left: 30rpx;
				width: 36rpx;
				height: 36rpx;
			}
		
			input {
				margin-left: 20rpx;
				height: 78rpx;
				width: 80%;
				line-height: 78rpx;
				font-size: 28rpx;
			}
		
			.defaultInput {
				color: #d1d1d1;
				line-height: 78rpx !important;
				font-size: 28rpx;
			}
		}
		.info{
			margin: 20rpx 5%;
			padding: 0 20rpx;
			display: flex;
			align-items: center;
			view{
				font-size: 32rpx;
				font-weight: 500;
				margin-right: 50rpx;
			}
		}
		.tip{
			display: flex;
			background-color: #f7f6fb;
			color: #636364;
			text-align: center;
			line-height: 58rpx;
			font-size: 28rpx;
			.city{
				flex: 5;
			}
			.school{
				flex: 7;
			}
		}
		.list{
			background-color: #f7f6fb;
			display: flex;
			font-size: 30rpx;
			text-align: center;
			min-height: 80vh;
			.cell{
				margin: 0 auto;
				background-color: #fff;
				line-height: 88rpx;
				width: 80%;
				padding: 0 10%;
				text-overflow: ellipsis;
				white-space: nowrap;
				overflow: hidden;
				border-bottom: 1rpx solid #f7f6fb;
			}
			.checked{
				color: #ee0000;
				background-image: url(../../static/volunteer/checked_icon.png);
				background-position: 90% center;
				background-repeat: no-repeat;
				background-size: 20rpx;
			}
			.listLeft{
				// flex: 5;
				background-color: #fff;
				.cell{
					padding-right: 50rpx;
					border-right: 4rpx solid #f7f6fb;
				}
			}
			.listRight{
				flex: 7;
				padding-bottom: 100rpx;
				background-color: #fff;
			}
		}
		.sure{
			width: 90%;
			background: linear-gradient(to bottom, #ffe592, #ffc343);
			border-radius: 50rpx;
			line-height: 78rpx;
			text-align: center;
			color: #fff;
			font-size: 28rpx;
			position: fixed;
			bottom: 40rpx;
			left: 5%;
			right: 5%;
		}
	}
</style>